.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 30px;
  margin-bottom: 60px;
  background: #fcfcfc;
  box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}
.logoImg {
  width: 209px;
  height: 48px;
}
.list {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  align-items: center;
}
.isActive {
  color: #0059b2;
}

.main_logo {
  width: 173px;
  height: 41px;
  margin-left: 136px;
}

.button {
  width: 113px;
  margin-right: 136px;
}

.userProfile {
  font-size: 16px;
  font-weight: 500;
  margin-right: 136px;
  text-decoration: underline dotted;
}
